.Title
{
   
    font-family: "Silkscreen", sans-serif;
    font-weight: 400;
    font-style: normal;
    text-align: center;
    color: #fff; /* Text color */
  text-shadow: 0 0 5px #00f, 0 0 10px #00f, 0 0 15px #00f, 0 0 20px #00f, 0 0 25px #00f, 0 0 30px #00f, 0 0 35px #00f; /* Shadow effect */
}

.champs_DISP
{
    height: 90%;
    width: 100%;
    overflow: hidden;
}

.champ_BD
{
    height: 100%;
    width: 100%;
}

.user_bars
{
    justify-content: flex-end;
    display: flex;
    background-color: rgb(41, 41, 41);
    gap: 60%;
    margin-bottom: 10px;
    border-radius: 10px;
    padding: 10px;
    justify-content: center;
}


.profile_pic_Div
{
    height: 30px;
    width: 30px;
    background-color: rgb(97, 97, 97);
    border-radius: 20px;
   
}

.Name_PP
{
    display: flex;
    align-items: center;
    gap: 10px;
}

.player-name
{
    color: rgb(124, 155, 255);
   
    font-family: "Shippori Antique B1", sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 13px;
 
}


.wins
{
    font-size: 12px;
    color: rgb(62, 62, 255);
}