.copy_s
{
    color: rgb(183, 183, 183);
    font-size: 13px;
    text-align: center;
}

.inner_Cont
{
    padding: 10px;
    width: 80%;
    background-color: rgb(43, 43, 43);
    border-radius: 10px;
}

.full_Cont
{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

h2
{
    color: rgb(195, 146, 255);
    text-align: center;
    font-family: "Silkscreen", sans-serif;
    text-shadow: 0 0 5px #00f, 0 0 10px #00f, 0 0 15px #00f, 0 0 20px #00f, 0 0 25px #00f, 0 0 30px #00f, 0 0 35px #00f; /* Shadow effect */
}