.room_div
{
    height: 100%;
    width: 100%;
}

.bar
{
    width: 100%;
    height: 7%;
  
}

.rooms
{
    height: 93%;
    width: 100%;
    overflow: hidden;
    overflow-y: scroll;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
   
}

.roon_TBF
{
    background-color: rgb(46, 46, 46);
    width: 100%;
    padding: 10px;
    display: flex;
    align-items: center;
   
}

.image_div
{
    height: 50%;
    width: 7%;
    flex-shrink: 0;
}

img
{
    height: 100%;
    width: 100%;
    object-fit: contain;
}

.room_subs
{
    display: flex;
    align-items: center;
    gap: 10px;
    width: 70%;
    flex-shrink: 0;
}

.names
{
    color: aquamarine;
    font-size: 12px;
    text-shadow: 0 0 5px #00f, 0 0 10px #00f, 0 0 15px #00f, 0 0 20px #00f, 0 0 25px #00f, 0 0 30px #00f, 0 0 35px #00f; /* Shadow effect */
}

.j_th
{
    font-size: 12px;
    background-color: rgb(106, 111, 255);
    padding: 7px;
    border-radius: 5px;
}

.icons_Bar
{
    padding: 10px;
    color: rgb(89, 139, 255);
    display: flex;
    justify-content: end;
    gap: 10%;
    align-items: center;
}

