.init_mdiv
{
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
 
}

.div_m
{

    width: 80%;
    background-color: rgb(36, 36, 36);
    border-radius: 20px;
    padding: 10px;
    display: block;
}

input
{
    padding: 10px;
    width: 95%;
    border: none;
    background-color: rgb(110, 110, 110);
    margin-bottom: 30px;
    border-radius: 10px;
    outline: none;
   
}
.div_un
{
    width: 80%;
    background-color: rgb(36, 36, 36);
    border-radius: 20px;
    padding: 10px;
    display: block;
}

.explanation
{
    color: rgb(172, 172, 172);
    text-align: center;
    font-size: 15px;
}

button
{
    padding: 10px;
    border: none;
    background-color: rgb(85, 85, 85);
    width: 100%;
    border-radius: 10px;
    font-weight: 1000;
    color: rgb(31, 31, 31);
}

.tt
{
    text-align: center;
    font-weight: 1000;
    font-size: 17px;
    color: rgb(161, 161, 161);
}
